import React from "react";
import Tooltip from "@material-ui/core/Tooltip";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import { ColorLens, Lens } from "@material-ui/icons";
import { IconButton } from "@material-ui/core";

const Colors = () => (
  <div>
    <IconButton color="primary" aria-label="Pick note" size="small">
      <Lens />
    </IconButton>
    <IconButton color="secondary" aria-label="Pick note" size="small">
      <Lens />
    </IconButton>
  </div>
);

const ColorsButton = ({ handleTooltipOpen }) => {
  const onOpen = e => {
    e.stopPropagation();
    handleTooltipOpen();
  };
  return (
    <IconButton
      color="primary"
      aria-label="Delete note"
      size="small"
      onClick={onOpen}
    >
      <ColorLens />
    </IconButton>
  );
};

const ColorsPalette = props => {
  const [open, setOpen] = React.useState(false);

  function handleTooltipClose() {
    setOpen(false);
  }
  function handleTooltipOpen() {
    setOpen(true);
  }

  return (
    <ClickAwayListener onClickAway={handleTooltipClose}>
      <Tooltip
        PopperProps={{
          disablePortal: true
        }}
        onClose={handleTooltipClose}
        open={open}
        disableFocusListener
        disableHoverListener
        disableTouchListener
        title={<Colors />}
        placement="top"
      >
        <ColorsButton handleTooltipOpen={handleTooltipOpen} />
      </Tooltip>
    </ClickAwayListener>
  );
};

export default ColorsPalette;
